import React, { useState, useEffect } from "react";
import styled from "styled-components";
import SwiperCore, { Navigation, Pagination, A11y } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";

import { firebaseapp } from "../../utils";
import { useTheme } from "../../styles";
import "firebase/storage"

import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";

SwiperCore.use([Navigation, Pagination, A11y]);

const Container = styled.div`
	padding: 5vw;
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	font-weight: 700;
	text-align: center;
`;

const Text = styled.h4`
	font-family: "Crimson Text", sans-serif;
	font-weight: 400;
	text-align: center;
	padding: 0 5vw 0 5vw;
`;

const SubHead = styled.h2`
	font-family: "Open Sans", sans-serif;
	font-weight: 500;
	padding: 1.5rem 0 1.5rem 0;
`;

const Img = styled.img`
	height: 40vh;
`;

const OurWorks: React.FC = () => {
	const { screens } = useTheme();

	const [living_room_imgs, setLivingRoomImgs] = useState([]);
	const [modular_kitchen_imgs, setModularKitchenImgs] = useState([]);
	const [dining_room_imgs, setDiningRoomImgs] = useState([]);
	const [bed_room_imgs, setBedRoomImgs] = useState([]);
	const [false_ceiling_imgs, setFalseCeilingImgs] = useState([]);
	const [flooring_imgs, setFlooringImgs] = useState([]);

	// useEffect(() => {
	// 	const x = new ImageStore();
	// 	x.interior_decorations.living_rooms().then(imgs => setLivingRoomImgs(imgs));
	// 	x.interior_decorations.modular_kitchens().then(imgs => setModularKitchenImgs(imgs));
	// 	x.interior_decorations.dining_rooms().then(imgs => setDiningRoomImgs(imgs));
	// 	x.interior_decorations.bed_rooms().then(imgs => setBedRoomImgs(imgs));
	// 	x.interior_decorations.false_ceilings().then(imgs => setFalseCeilingImgs(imgs));
	// 	x.interior_decorations.floorings().then(imgs => setFlooringImgs(imgs));
	// }, []);i

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("interior_decorations/living_rooms")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setLivingRoomImgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("interior_decorations/modular_kitchens")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setModularKitchenImgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("interior_decorations/dining_rooms")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setDiningRoomImgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("interior_decorations/bed_rooms")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setBedRoomImgs(urls);
				});
			});

		firebaseapp
			.storage()
			.ref("interior_decorations/false_ceilings")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setFalseCeilingImgs(urls);
				});
			});
		
		firebaseapp
			.storage()
			.ref("interior_decorations/floorings")
			.listAll()
			.then(({ items }) => {
				Promise.all(items.map(imgRef => imgRef.getDownloadURL())).then(urls => {
					setFlooringImgs(urls);
				});
			});
	}, []);

	return (
		<Container>
			<Heading>Our Works</Heading>
			<Text>
				Lorem ipsum dolor sit amet consectetur adipisicing elit. Inventore, fuga eveniet perspiciatis provident
				omnis necessitatibus laudantium odit debitis quisquam autem modi nostrum facere, mollitia nemo in, iusto
				consectetur iure accusantium!
			</Text>

			<br></br>
			<br></br>

			<SubHead>Living Rooms</SubHead>

			<Swiper spaceBetween={50} slidesPerView={screens.md ? 1 : 3} navigation pagination={{ clickable: true }}>
				{living_room_imgs.map(url => (
					<SwiperSlide>
						<div style={{ textAlign: "center" }}>
							<Img src={url}></Img>
						</div>
					</SwiperSlide>
				))}
			</Swiper>

			<br></br>

			<SubHead>Modular Kitchens</SubHead>

			<Swiper spaceBetween={50} slidesPerView={screens.md ? 1 : 3} navigation pagination={{ clickable: true }}>
				{modular_kitchen_imgs.map(url => (
					<SwiperSlide>
						<div style={{ textAlign: "center" }}>
							<Img src={url}></Img>
						</div>
					</SwiperSlide>
				))}
			</Swiper>

			<br></br>

			<SubHead>Dining Rooms</SubHead>

			<Swiper spaceBetween={50} slidesPerView={screens.md ? 1 : 3} navigation pagination={{ clickable: true }}>
				{dining_room_imgs.map(url => (
					<SwiperSlide>
						<div style={{ textAlign: "center" }}>
							<Img src={url}></Img>
						</div>
					</SwiperSlide>
				))}
			</Swiper>

			<br></br>

			<SubHead>Bed Rooms</SubHead>

			<Swiper spaceBetween={50} slidesPerView={screens.md ? 1 : 3} navigation pagination={{ clickable: true }}>
				{bed_room_imgs.splice(0, 10).map(url => (
					<SwiperSlide>
						<div style={{ textAlign: "center" }}>
							<Img src={url}></Img>
						</div>
					</SwiperSlide>
				))}
			</Swiper>

			<br></br>

			<SubHead>False Ceilings</SubHead>

			<Swiper spaceBetween={50} slidesPerView={screens.md ? 1 : 2} navigation pagination={{ clickable: true }}>
				{false_ceiling_imgs.map(url => (
					<SwiperSlide>
						<div style={{ textAlign: "center" }}>
							<Img src={url}></Img>
						</div>
					</SwiperSlide>
				))}
			</Swiper>

			<br></br>

			<SubHead>Floorings</SubHead>

			<Swiper spaceBetween={50} slidesPerView={screens.md ? 1 : 3} navigation pagination={{ clickable: true }}>
				{flooring_imgs.map(url => (
					<SwiperSlide>
						<div style={{ textAlign: "center" }}>
							<Img src={url}></Img>
						</div>
					</SwiperSlide>
				))}
			</Swiper>

			<br></br>
		</Container>
	);
};

export default OurWorks;
