import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { firebaseapp } from "../../utils";
import "firebase/storage"

const Container = styled.div`
	display: flex;
	padding: 5vw;
	flex-direction: ${props => (props.theme.screens.md ? "column" : "row")};
	justify-content: space-around;
	align-items: center;
	background: url("/images/all_circles.svg");
`;

const TextContainer = styled.div`
	padding: 5vw;
`;

const Img = styled.img`
	width: ${props => (props.theme.screens.md ? "90%" : "50vw")};
	padding-bottom: ${props => (props.theme.screens.md ? "8vw" : "0vw")};
`;

const Heading = styled.h1`
	font-family: "Open Sans", sans-serif;
	color: ${props => props.theme.colors.white};
	font-weight: 700;
	font-size: 3rem;
`;

const SubText = styled.p`
	font-family: "Crimson Text", serif;
	font-weight: 300;
	font-size: 1.5rem;
	color: ${props => props.theme.colors.white};
`;

const Home: React.FC = () => {
	const [img, setImg] = useState("");

	useEffect(() => {
		firebaseapp
			.storage()
			.ref("landing_page/11.jpg")
			.getDownloadURL()
			.then(url => setImg(url));
	}, []);

	return (
		<Container>
			<TextContainer>
				<Heading>Interior Decorations</Heading>
				<SubText>
					[About us text goes here] Lorem, ipsum dolor sit amet consectetur adipisicing elit. Odit modi aut
					aperiam, fugit consectetur eveniet natus dicta iste non perferendis deserunt saepe harum!
					Perferendis sapiente veritatis, amet id nesciunt aspernatur.
				</SubText>
				<br></br>
			</TextContainer>
			<Img src={img}></Img>
		</Container>
	);
};

export default Home;
